import {request} from './request'

//请求首页的大量数据  包括轮播图   每日推荐的四个商品  
export function getHomeMultidata() {
  return request({
    url: 'getAllHomeGoods.do',
    method: 'post'
  })
}

//请求首页商品展示信息    传入两个参数 商品类型  页数  流行 新品 精选三种的第一页数据20条
export function getHomeGoods(typepage) {
  return request({
    url: 'getKindGoods.do',
    method: 'post',
    data: {
      gkind: typepage
    }
  })
}