x<template>
  <div id="swipers">
    <div class="swiper" @touchstart="touchStart" @touchmove="touchMove" @touchend="touchEnd">
      <slot></slot>
    </div>
    <div class="indicator">
      <!--当显示底下时 并且滚动图片大于1时  显示-->
      <slot name="indicator" v-if="showIndicator && slideCount > 1">
        <!--当图片的下标与按钮的下标-1相同时为红色-->
        <div v-for="(item, index) in slideCount"  :key="index" class="initem" :class="{active:index == currentIndex - 1}" @click="changeItem(index)"></div>
      </slot>
    </div>

  </div>
</template>

<script>
  export default {
    name: 'Swiper',
    props: {
      //轮播图自动跳转间隔   默认3s
      interval: {
        type: Number,
        default: 3000
      },
      //动画过渡时间  默认3s
      animDuration: {
        type: Number,
        default: 500
      },
      //轮播图滑动多少切换页面
      moveRatio: {
        type: Number,
        default: 0.25
      },
      //是否显示底下的指示
      showIndicator: {
        type: Boolean,
        default: true
      }
    },
    data() {
      return {
        slideCount: 0, // 元素个数
        totalWidth: 0, // swiper的宽度
        swiperStyle: {}, // swiper样式
        currentIndex: 1, // 当前的index
        scrolling: false, // 是否正在滚动
      }
    },
    //在页面渲染完后调用
    mounted () {
      this.$nextTick(() => {
        //操作DOM  
        setTimeout(() => {
          //在前后添加swiper-item
          this.handleDom();
        
          //开启计时器
          this.startTimer();
        },800)
      })
      
    },
    methods: {
      //开启定时器
      startTimer() {
        //此时this只想window
        this.playTimer = window.setInterval(() => {
          //当前index+1
          this.currentIndex++;
          //console.log(this.playTimer);
          //滚动图片
          this.scrollContent(-this.currentIndex * this.totalWidth);
          //  console.log(this.totalWidth);
          //  console.log(-this.currentIndex * this.totalWidth);
          //  console.log(this.currentIndex);
          //传入间歇调用的时间
        },this.interval);
      },
      //关闭定时器
      stopTimer() {
        window.clearInterval(this.playTimer);
      },
      //滚动到正确的位置
      scrollContent(cuurentposition) {
        //设置处于正在滚动状态
        this.scrolling =true;
        //设置图片外层div的过度动画属性
        this.swiperStyle.transition = 'transform ' + this.animDuration + 'ms';
        //开始滚动图片
        this.setTransform(cuurentposition);
        //判断滚动到的位置
        this.checkPosition();
        //滚动完成
        this.scrolling = false;
      },
      //设置图片滚动的位置  传入移动的距离
      setTransform(position) {
        //设置3d动画
        this.swiperStyle.transform = 'translate3d(' + position + 'px, 0, 0)';
        this.swiperStyle['-webkit-transform'] = 'translate3d(' + position + 'px, 0, 0)';
        this.swiperStyle['-ms-transform'] = 'translate3d(' + position + 'px, 0, 0)';
        
      },
      //检查滚动的位置
      checkPosition() {
         window.setTimeout(() => {
          // 校验正确的位置
          //将动画过渡时间该为0
          this.swiperStyle.transition = '0ms';
          //如果下标>=元素个数+1   在最后一张图片时 将图片移动到第二张图片的位置
          if (this.currentIndex >= this.slideCount + 1) {
            this.currentIndex = 1;
            this.setTransform(-this.currentIndex * this.totalWidth);
            //如果下表<=0  在第一张图片时   将图片移动到倒数第二章   下标改为元素的个数 
          } else if (this.currentIndex <= 0) {
            this.currentIndex = this.slideCount;
            this.setTransform(-this.currentIndex * this.totalWidth);
          }
         },this.animDuration);
      },


      //操作DOM的方法
      handleDom() {
        //获取要操作的元素
        //获取class为swiper的第一个元素
        let swiperEl = document.querySelector(".swiper");
        //获取该元素下所有的类为swiper-item 的元素
        let swiperEls = swiperEl.getElementsByClassName("slide");
        //保存该元素的个数
        this.slideCount = swiperEls.length;
        //如果元素个数大于1 在前后分别添加一个swiper-item
        if(this.slideCount > 1) {
          //复制第一个节点和它的子节点（true）    为false时，只复制当前节点不复制子节点     
          let cloneFirst = swiperEls[0].cloneNode(true);
          //复制最后一个节点及其子节点
          let clontlast = swiperEls[this.slideCount - 1].cloneNode(true);
          //将复制的第一个节点插入到当前第一个节点之前
          swiperEl.insertBefore(clontlast, swiperEls[0]);
          //将复制的最后一个子节点追加到最后
          swiperEl.appendChild(cloneFirst);
          //保存元素的宽度和样式
          this.totalWidth = swiperEl.offsetWidth;
          this.swiperStyle = swiperEl.style;

          // 让swiper元素, 显示第一个(目前是显示前面添加的最后一个元素)
          this.setTransform(-this.totalWidth); 
        }
      },
      
      //拖动事件的处理
      //开始拖动 
      touchStart(e) {
        //如果正在滚动, 不可以拖动
        if (this.scrolling) {
          return;
        } 
        //停止定时器
        this.stopTimer();
        //保存开始滚动的位置
        this.startX = e.touches[0].pageX;
      },
      //正在拖动
      touchMove(e) {
        //计算出用户拖动的距离 = 结束时的位置-开始时的位置
        this.currentX = e.touches[0].pageX;
        this.distance = this.currentX - this.startX;
        //计算出图片当前所处位置的距离 = -下标*宽度
        let currentPosition = -this.currentIndex * this.totalWidth;
        //计算出相对距离 = 用户拖动的距离 + 图片的位置
        let moveDistance = this.distance + currentPosition;

        //设置当前的位置
        this.setTransform(moveDistance);
      },
      //拖动完成
      touchEnd(e) {
        //获取移动的距离   取绝对值
        let currentMove = Math.abs(this.distance);
        //判断最终的距离   
        //没有移动
        if (this.distance === 0) {
          return
          //向右拖动  并且移动的距离大于  宽度*传入的参数
        } else if (this.distance > 0 && currentMove > this.totalWidth * this.moveRatio) { 
          //下表-1
          this.currentIndex--
          //向左拖动   
        } else if (this.distance < 0 && currentMove > this.totalWidth * this.moveRatio) {
          //下标+1
          this.currentIndex++
        }
        //移动到正确的位置
        this.scrollContent(-this.currentIndex * this.totalWidth);

        //移动完成后重新开启定时器
        this.startTimer();
      },
      changeItem(index){
        //关闭定时器
        this.stopTimer();
        //修改位置
        this.currentIndex = index + 1;
        this.scrollContent(-this.currentIndex * this.totalWidth);
        //开启定时器
        this.startTimer();
      }




    }
  }
</script>

<style scoped>
  /*设置轮播图的外层 */
  #swipers{
    /*隐藏div外的内容    相对定位 */
    overflow: hidden;
    position: relative;
  }
  /*设置轮播图片的样式 */
  .swiper{
    /*开启flex布局 */
    display: flex;
  }
  .indicator{
    display: flex;
    justify-content: center;
    position: absolute;
    width: 100%;
    bottom: 8px;
  }
  .initem{
    box-sizing: border-box;
    width: 8px;
    height: 8px;
    border-radius: 4px;
    background-color: white;
    line-height: 8px;
    text-align: center;
    font-size: 12px;
    margin: 0 5px;
    cursor: pointer;
  }
 
  .active{
    background-color: rgba(212,62,46,1.0);
  }
</style>