import {debounce} from 'commons/utils'
import BackTop from 'content/backtop/BackTop'

//混入图片加载
export const itemImageMixin = {
  data () {
    return {
      itemListener: null
    }
  },
  mounted () {
    //解决scrollheight的问题
    //调用防抖函数
    const refresh = debounce(this.$refs.scroll.refresh, 50)
    //保存函数
    this.itemListener = () => {
      //刷新
     refresh()
    };
    //监听item中的图片加载
    this.$bus.$on('itemImageLoad', this.itemListener)
  }
}

//混入backtop
export const backTopMixin = {
  components: {
    BackTop
  },
  data () {
    return {
      isShowBackTop: false
    }
  },
  methods: {
    //使页面置顶
    backClick() {
      // 通过ref获取当前页面的scroll元素  在获取组件中的scroll对象   传入3个参数  x  ,y  , 动画的时间
        this.$refs.scroll.scrollTo(0, 0)
      }
  }
}