<template>
  <div class="goodslist">
    <goods-item v-for="(item, index) in goods" :key="index" :goods-item="item"></goods-item>
  </div>
</template>

<script>
  import GoodsItem from './GoodsItem'
  export default {
    name: 'GoodsList',
    components: {
      GoodsItem
    },
    //接受父组件的商品信息 并展示 
    //传递一个对象给子组件
    props: {
      goods: {
        type: Array,
        default() {
          return []
        }
      }
    }
  }
</script>

<style scoped>
  .goodslist{
    width: cale(100% - 100px);
    display: flex;
    /*根据宽度设置一行多少个 */
    flex-wrap: wrap;
    /*均等分 */
    justify-content: space-around;
  }
</style>