<template>
  <div class="goods" @click="itemClick">
    <img :src="goodsItem.gimage" alt="" @load="imgLoad">
    <div class="goods-info">
      <p>{{goodsItem.gtitle}}</p>
      <span class="price">￥{{goodsItem.gsize6}}</span>
      <span class="collect">{{goodsItem.gcollect}}</span>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'GoodsItem',
    props: {
      goodsItem: {
        type: Object,
        default() {
          return {}
        }
      }
    },
    methods: {
      //监听图片是否加载完成     解决scrollheight的高度问题      使用事件总线
      imgLoad() {
          this.$bus.$emit('itemImageLoad')
      },
      //跳转到详情页
      itemClick() {
        //如果在详情页先返回在跳转
        // if(this.$route.path.indexOf('/detail') != -1) {
        //   this.$router.back(); 
        //   this.$router.push('/detail/' + this.goodsItem.did)
        //   console.log(1)
        // }
        this.$router.push('/detail/' + this.goodsItem.gid)
      }
    }
  }
</script>

<style scoped>
 .goods{
   padding-bottom: 40px;
   position: relative;
   width: 46%;
 }
  .goods img{
    width: 100%;
  }
  /*商品下描述信息展示*/
  .goods-info{
    font-size: 12px;
    position:absolute;
    bottom: 5px;
    left: 0;
    right:0;
    overflow: hidden;
    text-align: center;
  }
  .goods-info p{
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin-bottom: 3px;
  }
  .goods-info .price{
    color: #ff8198;
    margin-right: 20px;
  }
  .goods-info .collect{
    position: relative;
  }
  /*设置收藏前的图标 */
  .goods-info .collect::before{
    content: '';
    position: absolute;
    left: -15px;
    top: 0;
    width: 14px;
    height: 14px;
    background: url('~assets/img/collect.svg') 0 0/14px 14px;
  }
</style>